/* Variables
 ============================== */
:root {
  --primary: #FF9500; }

/* Default styles
 ============================== */
body {
  min-height: 100vh;
  background-color: #000;
  color: #fff;
  font: 16px 'Inconsolata', monospace; }

*, *::before, *::after {
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

button {
  border: none;
  background: none; }
  button:focus {
    outline: none; }

/* Timer
====================== */

.timer {
  padding: 20px;
  min-height: 100vh; }
  .timer-panel {
    padding-top: 20%;
    text-align: center; }
  .timer-controls {
    margin-bottom: 50px;
    text-align: center; }
  .timer-display {
    margin-bottom: 20px;
    padding: 10px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    font-size: 4rem;
    line-height: 1.1;
    text-align: center;
    color: var(--primary); }
  .timer__milliseconds {
    font-size: 0.5rem; }
  .timer__btn {
    padding: 15px 20px;
    -webkit-transition: all .2s ease;
    -o-transition: all .2s ease;
    transition: all .2s ease;
    border-radius: 10px;
    background-color: var(--primary);
    -webkit-box-shadow: 0 3px 0 #ad6600;
    box-shadow: 0 3px 0 #ad6600;
    font-family: inherit;
    font-size: 1.3rem;
    color: #fff; }
    .timer__btn:active, .timer__btn:not(.timer__btn--focus):focus {
      -webkit-box-shadow: none;
      box-shadow: none;
      -webkit-transform: translateY(3px);
      -ms-transform: translateY(3px);
      transform: translateY(3px); }
  .timer-setting {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center; }
    .timer-setting__row {
      margin: 0 10px; }
    .timer-setting__label {
      display: block;
      margin-bottom: 10px; }
    .timer-setting__select {
      width: 100px;
      height: 40px;
      padding: 10px; }
